import { useCallback, useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Icon } from '@iconify/react';
import { toast } from 'react-toastify';

import { API_TICKET } from '../../config';

import axios from '../../utils/axios';

import { setSavedTicket } from '../../redux/slices/ticket';
import { dispatch, useSelector } from '../../redux/store';

import useLocales from '../../hooks/useLocales';
import { useUploadForm } from '../../hooks/useUploadForm';

import DropzoneFileUpload from '../../components/form/DropzoneFileUpload';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import { FloatingInput } from '../../components/form/FloatingInput';

const selectAccountOptions = [
    { label: "PDF", value: "pdf", requireAccount: false, accept: { pdf: [".pdf"] } },
    { label: "Apple account", value: "apple", requireAccount: true, accept: { images: [".jpg", ".png", ".bmp", ".webp"] } }
]

export default function TicketUpload({ setStep }) {
    const { tid } = useParams();
    const { saved } = useSelector((state) => state.ticket);
    const { t } = useLocales();
    const [loading, setLoading] = useState(false);
    const [selectedFile, setSelectedFile] = useState(saved?.originFile);
    const [requireAccount, setRequireAccont] = useState(false);
    const [requireAccountDetail, setRequireAccontDetail] = useState('');
    const [selectedAccount, setSelectedAccount] = useState();

    const { uploadForm, progress, isError, setProgress, serverMessage, uploadResult } = useUploadForm(
        API_TICKET.uploadFile
    );

    const handleDropFile = useCallback(
        async (acceptedFiles) => {
            if (acceptedFiles.length) {
                if (selectedFile) {
                    await handleDeleteFile();
                }
                setSelectedFile(acceptedFiles[0]?.path);
                const form = new FormData();
                form.append('file', acceptedFiles[0]);
                form.append('ticketId', saved?._id);
                form.append('originFile', acceptedFiles[0]?.path);

                await uploadForm(form);
            }
        },
        // eslint-disable-next-line
        [saved]
    );

    const onChangeAccount = async (account) => {
        if (selectedFile && requireAccount !== account.requireAccount)
            return toast.warning(t("description.delete_ticket"));
        setRequireAccont(account.requireAccount);
        setSelectedAccount(account);
    }

    const handleDeleteFile = async () => {
        setLoading(true);
        await axios
            .post(API_TICKET.deleteTicket, {
                ticketId: saved?._id
            })
            .then(({ data }) => {
                setSelectedFile('');
                setLoading(false);
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    };

    const onNext = () => {
        setLoading(true);
        if (requireAccount && !requireAccountDetail)
            return toast.warning(t("description.type_require_account"));
        axios
            .post(API_TICKET.saveTicketAccount, {
                ticketId: saved?._id,
                requireAccountDetail,
                requireAccount
            })
            .then((res) => {
                setStep(2);
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    };

    const onPrev = () => {
        if (!tid) setStep(0);
    };

    useEffect(() => {
        setSelectedAccount(selectAccountOptions[0]);
    }, [])

    useEffect(() => {
        if (uploadResult && uploadResult != null) {
            dispatch(setSavedTicket(uploadResult));
        }
    }, [uploadResult]);

    useEffect(() => {
        if (saved && !requireAccount) {
            setRequireAccont(saved?.requireAccount);
            setRequireAccontDetail(saved?.requireAccountDetail || '');
            setSelectedFile(saved.originFile);
        }
    }, [saved, requireAccount]);

    useEffect(() => {
        if (progress === 100) {
            setTimeout(() => {
                setProgress(0);
            }, 500);
        }
        // eslint-disable-next-line
    }, [progress]);

    const disabledForward = requireAccount
        ? !!requireAccountDetail.trim() && !isError
        : !isError && (progress === 100 || saved?.originFile);

    return (
        <div className="flex flex-col gap-8 justify-center items-center px-2">
            <div className="flex flex-col gap-4  w-full">
                <h3 className="text-4xl md:text-[64px]">{!tid ? t('title.add_new_ticket') : t('title.edit_ticket')}</h3>
                <p className="text-stone-400">{t('description.add_new_ticket')}</p>
            </div>

            <div className="w-full">
                <div className="grid md:grid-cols-2 gap-4" >
                    <div className="dropdown">
                        <div tabIndex={0} >
                            <GradientBorderWrapper className="px-5 py-3" >
                                <div className="flex justify-between" >
                                    <p className="">{selectedAccount?.label}</p>
                                    <Icon icon="ic:outline-keyboard-arrow-down" className='text-2xl' />
                                </div>
                            </GradientBorderWrapper>
                        </div>
                        <ul className="dropdown-content menu shadow bg-base-200 rounded-xl w-full">
                            <GradientBorderWrapper>
                                <li className="p-4 border-b border-stone-600">
                                    <p className="text-base text-primary p-0 hover:bg-inherit">{t("description.select_ticket_type")}</p>
                                </li>
                                {selectAccountOptions.map((option, i) => (
                                    <li key={i}>
                                        <Link to="#" className={`p-4 flex flex-row justify-between border-stone-600 ${selectedAccount?.value === option.value && "active"} ${i !== selectAccountOptions.length - 1 ? "border-b" : "rounded-b-xl"}`} onClick={() => onChangeAccount(option)}  >
                                            {option.label}
                                        </Link>
                                    </li>
                                ))}
                            </GradientBorderWrapper>
                        </ul>
                    </div>

                    {requireAccount && (
                        <FloatingInput
                            className="input w-full"
                            required
                            value={requireAccountDetail}
                            onChange={(e) => {
                                setRequireAccontDetail(e.target.value);
                            }}
                            label={t("description.type_require_account")}
                        />
                    )}
                </div>

                <div className="mt-8" />

                {selectedFile && (
                    <>
                        <GradientBorderWrapper>
                            <div className="w-full p-2 md:p-6 flex flex-col gap-2">
                                <div className="flex gap-2 justify-between w-full">
                                    <div className="flex gap-2 md:gap-6 items-center">
                                        <div className="rounded-full w-10 md:w-16 h-10 md:h-16 bg-secondary border-primary border flex items-center justify-center flex-1 min-w-[36px]">
                                            <svg
                                                style={{ width: 31, height: 24 }}
                                                width="78"
                                                height="70"
                                                viewBox="0 0 78 70"
                                                fill="none"
                                                xmlns="http://www.w3.org/2000/svg"
                                            >
                                                <path
                                                    d="M38.9931 35.0223V67.9863M38.9931 35.0223L29.8365 42.3477M38.9931 35.0223L48.1498 42.3477M13.479 24.4629C10.0149 25.3346 6.98834 27.4412 4.96793 30.387C2.94751 33.3328 2.07233 36.915 2.50682 40.4606C2.9413 44.0062 4.65554 47.2711 7.32746 49.6419C9.99938 52.0127 13.4451 53.3262 17.0172 53.3356H20.6798"
                                                    stroke="#F2B705"
                                                    strokeWidth="4"
                                                    strokeLinecap="round"
                                                    strokeLinejoin="round"
                                                />
                                                <path
                                                    d="M53.0209 17.2123C51.769 12.2551 48.6791 7.95946 44.3775 5.19604C40.0758 2.43262 34.8844 1.40824 29.8555 2.33054C24.8266 3.25285 20.3365 6.05284 17.2955 10.1629C14.2545 14.273 12.89 19.3856 13.4788 24.4644C13.4788 24.4644 14.0392 27.6948 15.1856 29.5262"
                                                    stroke="#F2B705"
                                                    strokeWidth="4"
                                                    strokeLinecap="round"
                                                    strokeLinejoin="round"
                                                />
                                                <path
                                                    d="M57.3066 53.3346C59.8925 53.3329 62.4488 52.7834 64.8072 51.7225C67.1655 50.6616 69.2724 49.1133 70.9892 47.1794C72.7061 45.2456 73.9939 42.97 74.7679 40.5026C75.5419 38.0352 75.7845 35.4318 75.4799 32.8638C75.1752 30.2958 74.3301 27.8215 73.0003 25.6037C71.6704 23.3858 69.886 21.4748 67.7643 19.9963C65.6427 18.5178 63.2319 17.5054 60.6908 17.0258C58.1497 16.5461 55.5358 16.6101 53.0212 17.2135L48.1499 18.5393"
                                                    stroke="#F2B705"
                                                    strokeWidth="4"
                                                    strokeLinecap="round"
                                                    strokeLinejoin="round"
                                                />
                                            </svg>
                                        </div>
                                        <h5 className="text-sm md:text-[28px] md:leading-9 overflow-hidden text-ellipsis">{selectedFile}</h5>
                                        <button
                                            className="btn btn-primary px-1 md:px-4 capitalize md:py-2 rounded-[100px] rounded-2xl text-sm md:text-[19px] h-auto btn-outline btn-disable"
                                            disabled={tid ? true : false}
                                        >
                                            {t('action.for_sale')}
                                        </button>
                                    </div>
                                    <div className="flex justify-center items-center">
                                        <button
                                            className="btn btn-circle btn-sm w-6 h-6 md:w-12 md:h-12 text-2xl"
                                            onClick={handleDeleteFile}
                                            style={{ backgroundColor: 'rgba(253, 54, 77, 0.3)' }}
                                        >
                                            <Icon icon="tabler:trash" className="text-error" />
                                        </button>
                                    </div>
                                </div>
                                {progress > 0 && (
                                    <div className="p-2 flex items-center justify-center w-full">
                                        <progress
                                            className={`progress ${isError ? 'progress-error' : 'progress-primary'}`}
                                            value={progress}
                                            max={100}
                                        />
                                    </div>
                                )}
                                {isError && serverMessage !== '' && (
                                    <div className="w-full p-2">
                                        <span className="text-error">{serverMessage}</span>
                                    </div>
                                )}
                            </div>
                        </GradientBorderWrapper>
                        <div className="h-6" />
                        <GradientBorderWrapper
                            className="w-full p-4 event-active-bg"
                            style={{ justifyContent: 'start' }}
                        >
                            <div className='flex gap-4'>
                                <div>
                                    <svg
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                        fill="none"
                                        xmlns="http://www.w3.org/2000/svg"
                                    >
                                        <path
                                            d="M12 24C15.1826 24 18.2348 22.7357 20.4853 20.4853C22.7357 18.2348 24 15.1826 24 12C24 8.8174 22.7357 5.76516 20.4853 3.51472C18.2348 1.26428 15.1826 0 12 0C8.8174 0 5.76516 1.26428 3.51472 3.51472C1.26428 5.76516 0 8.8174 0 12C0 15.1826 1.26428 18.2348 3.51472 20.4853C5.76516 22.7357 8.8174 24 12 24ZM13.395 9.882L11.895 16.9395C11.79 17.4495 11.9385 17.739 12.351 17.739C12.642 17.739 13.0815 17.634 13.38 17.37L13.248 17.994C12.8175 18.513 11.868 18.891 11.0505 18.891C9.996 18.891 9.5475 18.258 9.8385 16.9125L10.9455 11.7105C11.0415 11.271 10.9545 11.112 10.515 11.0055L9.8385 10.884L9.9615 10.3125L13.3965 9.882H13.395ZM12 8.25C11.6022 8.25 11.2206 8.09196 10.9393 7.81066C10.658 7.52936 10.5 7.14782 10.5 6.75C10.5 6.35218 10.658 5.97064 10.9393 5.68934C11.2206 5.40804 11.6022 5.25 12 5.25C12.3978 5.25 12.7794 5.40804 13.0607 5.68934C13.342 5.97064 13.5 6.35218 13.5 6.75C13.5 7.14782 13.342 7.52936 13.0607 7.81066C12.7794 8.09196 12.3978 8.25 12 8.25Z"
                                            fill="#F2B705"
                                        />
                                    </svg>
                                </div>
                                <p className='text-sm' >{t('description.add_new_ticket_desc')}</p>
                            </div>
                        </GradientBorderWrapper>
                    </>
                )}

                {!selectedFile && <DropzoneFileUpload onDrop={handleDropFile} accept={selectedAccount?.accept} />}

                {/* <div className="h-6" />
                <div className="flex items-center gap-3">
                    <input
                        id="requireAccount"
                        className="toggle toggle-primary"
                        type="checkbox"
                        checked={requireAccount}
                        onChange={(e) => setRequireAccont(e.target.checked)}
                    />
                    <label htmlFor="requireAccount" className="flex flex-col justify-center">
                        {t('title.require_account')}
                    </label>
                </div>
                <div className="h-6" /> */}

            </div>

            {/* action buttons */}
            <div className="flex flex-col gap-8 justify-center items-center w-full">
                <div className=" w-full mb-8">
                    <div className="w-full flex justify-between">
                        <button
                            className="btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto btn-outline btn-disable"
                            disabled={tid ? true : false}
                            onClick={onPrev}
                        >
                            {t('action.back')}
                        </button>
                        <button
                            disabled={!disabledForward}
                            className={`btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto ${loading && 'loading'}`}
                            onClick={onNext}
                        >
                            {t('action.continue')}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
