import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { Icon } from '@iconify/react';
import { toast } from 'react-toastify';

import { API_TICKET } from '../../config';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import { useSelector } from '../../redux/store';

import axios from '../../utils/axios';
import { fNumber } from '../../utils/formatNumber';
import { displayEventTime } from '../../utils/formatTime';

import ResetConfirmModal from './ResetConfirmModal';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketFinalize({ setStep, setID }) {
    const { tid } = useParams();
    const { user } = useAuth();

    const { t } = useLocales();
    const { saved } = useSelector((state) => state.ticket);
    const [event, setEvent] = useState(saved?.event);
    const onReset = () => {
        document.querySelector('#reset-confirm-modal').click();
    };

    const onCreateAD = () => {
        axios
            .post(API_TICKET.finializeTicket, { ticketId: saved?._id })
            .then((res) => {
                if (res.status === 200) {
                    setID(saved?._id);
                    setStep(6);
                } else
                    toast.warning(res.data?.message || res.data?.data);
            })
            .catch((err) => toast.warning(err.message));
    };
    const onClose = (action) => {
        document.querySelector('#reset-confirm-modal').click();
        if (action !== 'cancel') setStep(0);
    };

    useEffect(() => {
        if (saved) {
            const _event = saved?.event;
            const eventType = _event.types?.filter((t) => t.typeId === saved?.eventTypeId)[0];
            setEvent({ ..._event, eventType });
        }
    }, [saved]);

    return (
        <div className="flex flex-col gap-8 justify-center items-center px-2">
            <div className="flex flex-col gap-4 w-full">
                <h3 className="text-4xl md:text-[64px]">{t('title.ticket_finalize')}</h3>
                <p className="text-stone-400  md:w-1/2">{t('description.ticket_finalize')}</p>
                <progress className="progress progress-success mt-12 w-full" value="100" max="100" />
            </div>

            <div className="w-full mt-8 flex flex-col gap-4">
                <GradientBorderWrapper>
                    <div className="flex flex-col sm:flex-row gap-2 justify-between px-6 py-[30px] items-center">
                        <p className="text-stone-400 md:w-1/2">{t('description.add_new_ticket_revise')}</p>
                        <button className="btn btn-primary capitalize py-4 px-8 text-[19px] h-auto">{t('action.make_ticket')}</button>
                    </div>
                </GradientBorderWrapper>
            </div>
            <div className="w-full mb-8 flex flex-col gap-4">
                <GradientBorderWrapper>
                    {/* event */}
                    {!tid && (
                        <>
                            <div className="flex gap-2 justify-between p-6 items-center">
                                <div className="flex flex-col gap-1">
                                    <h5 className="text-[28px] font-medium">{t('title.event')}</h5>
                                    <p className="text-stone-400">
                                        {event?.name} <br />
                                        {event?.eventType?.name}
                                        <br />
                                        {event.dateTime && displayEventTime({ ...saved, containsTime: true })},&nbsp;
                                        {event?.place}
                                    </p>
                                    <span className="text-primary underline cursor-pointer text-sm mt-3" onClick={() => setStep(0)}>
                                        {t('action.edit')}
                                    </span>
                                </div>
                                <div>
                                    <Icon
                                        icon="material-symbols:check-small-rounded"
                                        className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                                    />
                                </div>
                            </div>
                            <div className="divider mx-6 my-2" />
                        </>
                    )}
                    {/* ticket */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.tickets')}</h5>
                            <p className="text-stone-400">
                                {t('description.sold_ticket', { count: saved?.seat.length })}
                            </p>
                            <span className="text-primary underline cursor-pointer text-sm mt-3" onClick={() => setStep(1)}>
                                {t('action.edit')}
                            </span>
                        </div>
                        <div>
                            <Icon
                                icon="material-symbols:check-small-rounded"
                                className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                            />
                        </div>
                    </div>
                    <div className="divider mx-6 my-2" />
                    {/* ticket detail */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_detail')}</h5>
                            <p className="text-stone-400">
                                {t('description.additional')}: {saved?.seat}
                            </p>
                            <span className="text-primary underline cursor-pointer text-sm mt-3" onClick={() => setStep(3)}>
                                {t('action.edit')}
                            </span>
                        </div>
                        <div>
                            <Icon
                                icon="material-symbols:check-small-rounded"
                                className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                            />
                        </div>
                    </div>
                    <div className="divider mx-6 my-2" />
                    {/* sell price */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_detail')}</h5>
                            <p className="text-stone-400">
                                R$ {fNumber(saved?.originPrice * 1.08)} por ingresso (você receberá R${' '}
                                {fNumber(saved?.originPrice * 1.05)} por ingresso)
                            </p>
                            <span className="text-primary underline cursor-pointer text-sm mt-3" onClick={() => setStep(3)}>
                                {t('action.edit')}
                            </span>
                        </div>
                        <div>
                            <Icon
                                icon="material-symbols:check-small-rounded"
                                className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                            />
                        </div>
                    </div>
                    <div className="divider mx-6 my-2" />
                    {/* phone number */}
                    {user?.phone && (
                        <div className="flex gap-2 justify-between p-6 items-center">
                            <div className="flex flex-col gap-1">
                                <h5 className="text-[28px] font-medium">{t('title.phone_number')}</h5>
                                <p className="text-stone-400">{user?.phone}</p>
                                <Link
                                    // htmlFor="phone-modal-check"
                                    className="text-primary underline cursor-pointer text-sm mt-3"
                                    to="/profile/changtelephone"
                                >
                                    {t('action.edit')}
                                </Link>
                            </div>
                            <div>
                                <Icon
                                    icon="material-symbols:check-small-rounded"
                                    className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                                />
                            </div>
                        </div>
                    )}

                    <div className="divider mx-6 my-2" />
                    {/* bank account */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.bank_account')}</h5>
                            <p className="text-stone-400">341 - 9649 - **** 4355</p>
                            <Link
                                // htmlFor="phone-modal-check"
                                className="text-primary underline cursor-pointer text-sm mt-3"
                                to="/profile"
                            >
                                {t('action.edit')}
                            </Link>
                        </div>
                        <div>
                            <Icon
                                icon="material-symbols:check-small-rounded"
                                className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                            />
                        </div>
                    </div>

                    <div className="divider mx-6 my-2" />
                    {/* identity verification */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.identity_verification')}</h5>
                            <Link
                                // htmlFor="phone-modal-check"
                                className="text-primary underline cursor-pointer text-sm mt-3"
                            // to="/profile"
                            >
                                {t('action.edit')}
                            </Link>
                        </div>
                        <div>
                            <Icon
                                icon="material-symbols:check-small-rounded"
                                className="text-white bg-success rounded-full w-6 h-6 md:w-12 md:h-12"
                            />
                        </div>
                    </div>
                </GradientBorderWrapper>
            </div>
            <div className="w-full mb-8 flex flex-col gap-4">
                <GradientBorderWrapper>
                    {/* validate */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.validate')}</h5>
                            <p className="text-stone-400">Público em FanTicket</p>
                            <span className="text-primary underline cursor-pointer text-sm mt-3">{t('action.edit')}</span>
                        </div>
                        <img src="/images/logo/logo-image.png" className="w-[60px] h-[60px]" alt="" />
                    </div>
                </GradientBorderWrapper>
            </div>
            <div className="flex flex-col gap-8 justify-center items-center w-full">
                <div className="w-full mb-8">
                    <div className="w-full flex justify-between">
                        <button className="btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto btn-outline" onClick={onReset}>
                            {t('action.reset')}
                        </button>
                        <button className="btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto" onClick={onCreateAD}>
                            {!tid ? t('action.create_ad') : t('action.update_ad')}
                        </button>
                    </div>
                </div>
            </div>
            <ResetConfirmModal ticketId={saved?._id} onClose={onClose} />
        </div>
    );
}
