import React from 'react';
import { useDropzone } from 'react-dropzone';
import GradientBorderWrapper from '../wrappers/GradientBorderWrapper';

export default function DropzoneFileUpload({ accept, onDrop = () => { } }) {
    const { getRootProps, getInputProps } = useDropzone({ accept, onDrop });

    return (
        <GradientBorderWrapper>
            <div {...getRootProps()} className="w-full p-10 md:p-16 flex flex-col items-center justify-center gap-8">
                <input {...getInputProps()} />
                <div className="rounded-full w-[150px] h-[150px] bg-secondary border-primary border flex items-center justify-center">
                    <svg
                        style={{ width: 74, height: 66 }}
                        width="78"
                        height="70"
                        viewBox="0 0 78 70"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M38.9931 35.0223V67.9863M38.9931 35.0223L29.8365 42.3477M38.9931 35.0223L48.1498 42.3477M13.479 24.4629C10.0149 25.3346 6.98834 27.4412 4.96793 30.387C2.94751 33.3328 2.07233 36.915 2.50682 40.4606C2.9413 44.0062 4.65554 47.2711 7.32746 49.6419C9.99938 52.0127 13.4451 53.3262 17.0172 53.3356H20.6798"
                            stroke="#F2B705"
                            strokeWidth="4"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                        />
                        <path
                            d="M53.0209 17.2123C51.769 12.2551 48.6791 7.95946 44.3775 5.19604C40.0758 2.43262 34.8844 1.40824 29.8555 2.33054C24.8266 3.25285 20.3365 6.05284 17.2955 10.1629C14.2545 14.273 12.89 19.3856 13.4788 24.4644C13.4788 24.4644 14.0392 27.6948 15.1856 29.5262"
                            stroke="#F2B705"
                            strokeWidth="4"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                        />
                        <path
                            d="M57.3066 53.3346C59.8925 53.3329 62.4488 52.7834 64.8072 51.7225C67.1655 50.6616 69.2724 49.1133 70.9892 47.1794C72.7061 45.2456 73.9939 42.97 74.7679 40.5026C75.5419 38.0352 75.7845 35.4318 75.4799 32.8638C75.1752 30.2958 74.3301 27.8215 73.0003 25.6037C71.6704 23.3858 69.886 21.4748 67.7643 19.9963C65.6427 18.5178 63.2319 17.5054 60.6908 17.0258C58.1497 16.5461 55.5358 16.6101 53.0212 17.2135L48.1499 18.5393"
                            stroke="#F2B705"
                            strokeWidth="4"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                        />
                    </svg>
                </div>
                <div className='flex flex-col gap-4 justify-center items-center w-[55%]' >
                    <h5 className='text-[28px]' >Faça upload de seus ingressos em {accept?.pdf ? "PDF" : "Image"}</h5>
                    <p className="text-stone-400 text-center">
                        Se o arquivo original tiver vários ingressos, faça upload de todos eles. Você poderá selecionar
                        somente aqueles que deseja vender.
                    </p>
                </div>
                <small className="text-primary">Soltar arquivos aqui ou clicar para selecionar</small>
            </div>
        </GradientBorderWrapper>
    );
}
