import { Icon } from '@iconify/react';
import { useNavigate } from 'react-router';
import CopyToClipboard from 'react-copy-to-clipboard';

import { HOST_API } from '../../config';
import useLocales from '../../hooks/useLocales';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketShareSocial({ id, setStep }) {
    const { t } = useLocales();
    const navigate = useNavigate();

    const url = `${HOST_API}ticket/ad/${id}`;
    return (
        <div className="flex flex-col gap-8 px-2">
            <div className="flex flex-col gap-4 w-full">
                <h3 className="text-4xl md:text-[64px]">{t('title.ticket_share')}</h3>
                <p className="text-stone-400 md:w-1/2">{t('description.ticket_share')}</p>
            </div>

            <div className="mb-8 flex flex-col mt-1 gap-6 md:w-2/5">
                <div className="flex flex-col  gap-8 justify-start items-center w-full">
                    <div className="flex justify-between gap-6 w-full mb-2">
                        <button className="btn btn-primary py-4 text-[19px] h-auto font-normal flex-1 capitalize bg-[#1877F2] border-[#1877F2] text-white hover:bg-blue-400">
                            <Icon icon="iconoir:facebook" width={20} className="mr-2" />
                            {t('action.facebook')}
                        </button>
                        <button className="btn btn-secondary py-4 text-[19px] h-auto font-normal capitalize w-2/5 bg-[#5ACBEF] text-white hover:bg-blue-500">
                            <Icon icon="tabler:brand-twitter" width={20} className="mr-1" />
                            {t('action.twitter')}
                        </button>
                    </div>
                    <div className="flex flex-col w-full items-center justify-start gap-4">
                        <CopyToClipboard text={url}>
                            <button className="btn btn-secondary py-4 text-[19px] h-auto font-normal capitalize w-full">
                                <Icon icon="ic:round-content-copy" width={20} className="text-primary mr-2" />
                                {t('action.copy_link')}
                            </button>
                        </CopyToClipboard>
                        <div className="w-full items-center justify-center flex">
                            <div
                                onClick={() => navigate(`/ticket/ad/${id}`)}
                                className="cursor-pointer text-primary underline"
                            >
                                {t('action.go_to_ad')}
                            </div>
                        </div>
                    </div>
                    {/* <GradientBorderWrapper>
                        <div className="flex px-2 items-center justify-start">
                            <Icon icon="ph:link-simple-bold" />

                            <input className="w-full flex-1 input" defaultValue={url} readOnly />
                            <CopyToClipboard text={url}>
                                <button className="btn btn-ghost btn-sm btn-circle">
                                    <Icon icon="ic:round-content-copy" width={20} className="text-primary" />
                                </button>
                            </CopyToClipboard>
                        </div>
                    </GradientBorderWrapper> */}

                    <GradientBorderWrapper className="w-full p-4 mt-8 event-active-bg rounded-lg">
                        <div className='flex gap-4'>
                            <div>
                                <Icon icon="mdi:information-variant-circle" className="text-primary w-[30px] h-full" />
                            </div>
                            <p className='text-sm' >{t('description.to_access_ticket')}</p>
                        </div>
                    </GradientBorderWrapper>
                </div>
            </div>
        </div>
    );
}
