import React, { useState } from 'react';
import { toast } from 'react-toastify';
import { Icon } from '@iconify/react';

import { API_TICKET } from '../../config';
import axios from '../../utils/axios';
import useLocales from '../../hooks/useLocales';
import { useSelector } from '../../redux/store';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketNote({ setStep }) {
    const { saved } = useSelector((state) => state.ticket);
    const [loading, setLoading] = useState(false);
    const [note, setNote] = useState(saved?.note);
    const [seat, setSeat] = useState(saved?.seat?.length ? saved?.seat : ['']);
    const [qrcode, setQrCode] = useState(saved?.qrcode?.length ? saved?.qrcode : ['']);

    const { t } = useLocales();

    const onNext = () => {
        setLoading(true);
        if (!saved?._id) {
            toast.error(t('error.please select the ticket'));
        }
        axios
            .post(API_TICKET.saveNote, { ticketId: saved?._id, note, seat, qrcode })
            .then((res) => {
                setStep(3);
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    };
    const onPrev = () => {
        setStep(1);
    };

    const addNewData = (e) => {
        setSeat([...seat, '']);
        setQrCode([...qrcode, '']);
        e.preventDefault();
    };
    const removeData = (e, index) => {
        seat.splice(index, 1);
        qrcode.splice(index, 1);
        setSeat([...seat]);
        setQrCode([...qrcode]);
        e.preventDefault();
    };

    return (
        <div className="flex flex-col gap-8 justify-center items-center px-2">
            <div className="flex flex-col gap-2 w-full mb-8">
                <h3 className="text-4xl md:text-[64px] md:leading-[70px]">{t('title.add_new_ticket')}</h3>
                <p className="text-stone-400">{t('description.add_new_ticket')}</p>
            </div>
            <div className="w-full mb-4 flex flex-col gap-4">
                <GradientBorderWrapper>
                    <div className="w-full p-6">
                        <textarea
                            className="textarea w-full border-0 text-base"
                            rows={9}
                            value={note}
                            onChange={(e) => setNote(e.target.value)}
                        />
                    </div>
                </GradientBorderWrapper>
                <GradientBorderWrapper className="w-full p-4 event-active-bg mb-4" style={{ justifyContent: 'start' }}>
                    <div className='flex gap-4 items-center'>
                        <div>
                            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path
                                    d="M12 24C15.1826 24 18.2348 22.7357 20.4853 20.4853C22.7357 18.2348 24 15.1826 24 12C24 8.8174 22.7357 5.76516 20.4853 3.51472C18.2348 1.26428 15.1826 0 12 0C8.8174 0 5.76516 1.26428 3.51472 3.51472C1.26428 5.76516 0 8.8174 0 12C0 15.1826 1.26428 18.2348 3.51472 20.4853C5.76516 22.7357 8.8174 24 12 24ZM13.395 9.882L11.895 16.9395C11.79 17.4495 11.9385 17.739 12.351 17.739C12.642 17.739 13.0815 17.634 13.38 17.37L13.248 17.994C12.8175 18.513 11.868 18.891 11.0505 18.891C9.996 18.891 9.5475 18.258 9.8385 16.9125L10.9455 11.7105C11.0415 11.271 10.9545 11.112 10.515 11.0055L9.8385 10.884L9.9615 10.3125L13.3965 9.882H13.395ZM12 8.25C11.6022 8.25 11.2206 8.09196 10.9393 7.81066C10.658 7.52936 10.5 7.14782 10.5 6.75C10.5 6.35218 10.658 5.97064 10.9393 5.68934C11.2206 5.40804 11.6022 5.25 12 5.25C12.3978 5.25 12.7794 5.40804 13.0607 5.68934C13.342 5.97064 13.5 6.35218 13.5 6.75C13.5 7.14782 13.342 7.52936 13.0607 7.81066C12.7794 8.09196 12.3978 8.25 12 8.25Z"
                                    fill="#F2B705"
                                />
                            </svg>
                        </div>
                        <p className='text-sm' >
                            {t('description.add_new_ticket_note')}
                        </p>
                    </div>
                </GradientBorderWrapper>

                {/* seat & qr info */}
                {seat?.map((_, index) => (
                    <React.Fragment key={index}>
                        <div className="flex gap-2 w-full justify-between">
                            <span className="text-2xl font-bold" />
                            <div className="flex gap-1 items-center">
                                <button className="btn btn-circle btn-secondary btn-sm" onClick={addNewData}>
                                    <Icon icon="ic:outline-plus" className="text-primary" />
                                </button>
                                <button
                                    className="btn btn-circle btn-secondary btn-sm"
                                    onClick={(e) => removeData(e, index)}
                                >
                                    <Icon icon="tabler:trash" />
                                </button>
                            </div>
                        </div>
                        <div className="flex gap-2 flex-col md:flex-row w-full">
                            <div className="flex-1">
                                <GradientBorderWrapper>
                                    <div className="flex w-full justify-between px-2 items-center">
                                        <Icon icon="ic:twotone-airline-seat-recline-extra" />
                                        <input
                                            className="input flex-1"
                                            placeholder='Seat'
                                            value={seat[index]}
                                            onChange={(e) => {
                                                const newSeat = [...seat];
                                                newSeat[index] = e.target.value;
                                                setSeat(newSeat);
                                            }}
                                        />
                                    </div>
                                </GradientBorderWrapper>
                            </div>

                            <div className="flex-1">
                                <GradientBorderWrapper>
                                    <div className="flex w-full justify-between px-2 items-center">
                                        <Icon icon="uiw:qrcode" />
                                        <input
                                            className="input flex-1"
                                            placeholder='QrCode'
                                            value={qrcode[index]}
                                            onChange={(e) => {
                                                const newQrcode = [...qrcode];
                                                newQrcode[index] = e.target.value;
                                                setQrCode(newQrcode);
                                            }}
                                        />
                                    </div>
                                </GradientBorderWrapper>
                            </div>
                        </div>
                    </React.Fragment>
                ))}

            </div>
            {/* action buttons */}

            <div className="w-full mb-8">
                <div className="w-full flex justify-between">
                    <button className="btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto btn-outline" onClick={onPrev}>
                        {t('action.back')}
                    </button>
                    <div className="flex justify-between gap-2 md:gap-6 items-center">
                        <p className='text-base text-primary underline'>
                            {t('action.skip')}
                        </p>
                        <button
                            // disabled={disabledForward}
                            className={`btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto ${loading && 'loading'}`}
                            onClick={onNext}
                        >
                            {t('action.continue')}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
