import { useEffect, useState } from 'react';
import { Icon } from '@iconify/react';
import { API_TICKET, HOST_API } from '../../config';

import useAuth from '../../hooks/useAuth';
import { useSelector } from '../../redux/store';

import useLocales from '../../hooks/useLocales';
import { useUploadForm } from '../../hooks/useUploadForm';
import axios from '../../utils/axios';

export default function TicketAvatar({ setStep }) {
    const { t } = useLocales();
    const { saved } = useSelector((state) => state.ticket);
    const { user } = useAuth();

    const [file, setFile] = useState(saved?.avatar || '');
    const [disabledForward, setDisabledForward] = useState(true);

    const imageChange = (event) => {
        if (event.target.files && event.target.files.length > 0) {
            setFile(event.target.files[0]);
        }
    };
    const [loading, setLoading] = useState(false);
    const { uploadForm, progress, isError } = useUploadForm(API_TICKET.uploadTicketAvatar);

    const onPrev = () => {
        setStep(3);
    };
    const onNext = () => {
        const form = new FormData();

        if (file && typeof file === 'object') {
            setLoading(true);
            form.append('ticketId', saved?._id);
            form.append('file', file);
            uploadForm(form);
        } else {
            setStep(5);
        }
    };

    const onSkip = () => {
        if (file && file === user.avatar) {
            const form = new FormData();
            form.append('ticketId', saved?._id);
            form.append('avatar', user.avatar);
            axios.post(`${API_TICKET.uploadTicketAvatar}`, form);
        }
        setStep(5);
    }

    useEffect(() => {
        if (saved !== null) {
            setFile(saved?.avatar || user.avatar || '');
        }
    }, [saved, user]);

    useEffect(() => {
        if (typeof file === 'object') {
            setDisabledForward(false);
        }
    }, [file]);

    useEffect(() => {
        if (progress === 100) setLoading(false);
        if (progress === 100 && !isError) {
            setDisabledForward(false);
            setStep(5);
        }
        // eslint-disable-next-line
    }, [progress, isError]);

    return (
        <div className="flex flex-col gap-8 justify-center items-center">
            <div className="flex flex-col gap-4 w-full">
                <h3 className="text-4xl md:text-[64px]">{t('title.ticket_avatar')}</h3>
                <p className="md:w-1/2 text-stone-400">{t('description.ticket_avatar')}</p>
            </div>
            <div className="w-full mt-8 mb-4 flex flex-col gap-4">
                <div className="flex flex-col items-center mb-4">
                    <div className="avatar mb-4 w-[200px] h-[200px] relative bg-base-200 rounded-full flex justify-center items-center">
                        {(file === '' || !file) && (
                            <div className="p-8">
                                <img src="/images/icons/ticket-cover-icon.png" className="w-full h-full" alt="" />
                            </div>
                        )}
                        {file && file !== '' && (
                            <img
                                src={typeof file === 'string' ? `${HOST_API}${file}` : URL.createObjectURL(file)}
                                alt=""
                                className=" rounded-full"
                            />
                        )}
                        <label
                            htmlFor="avatar"
                            className="bottom-0 right-0 absolute w-12 h-12 rounded-full bg-secondary flex items-center justify-center"
                        >
                            <Icon icon="ic:round-edit" className="text-primary text-[22px]" />
                        </label>
                        <input
                            type="file"
                            className="hidden"
                            accept=".jpg, .png, .bmp"
                            id="avatar"
                            onChange={imageChange}
                        />
                    </div>
                    <label className="text-sm text-primary underline cursor-pointer" htmlFor="avatar">
                        {t('action.select_photo')}
                    </label>
                </div>
            </div>

            <div className="w-full mb-8">
                <div className="h-6" />

                <div className="w-full flex justify-between">
                    <button className="btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto btn-outline" onClick={onPrev}>
                        {t('action.back')}
                    </button>
                    <div className="flex gap-2 md:gap-6 justify-center items-center">
                        <p className="text-sm text-primary underline cursor-pointer" onClick={onSkip}>
                            {t('action.skip')}
                        </p>
                        <button
                            onClick={onNext}
                            disabled={disabledForward}
                            className={`btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto ${loading && 'loading'}`}
                        >
                            {t('action.continue')}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
