import React, { useMemo, useState } from 'react';
import { FormProvider, useForm } from 'react-hook-form';

import { API_TICKET } from '../../config';

import axios from '../../utils/axios';
import { fNumber, fCurrency } from '../../utils/formatNumber';

import useLocales from '../../hooks/useLocales';

import { dispatch, useSelector } from '../../redux/store';
import { setSavedTicket } from '../../redux/slices/ticket';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketPrice({ setStep }) {
    const { t } = useLocales();
    const [loading, setLoading] = useState(false);
    const { saved } = useSelector((state) => state.ticket);

    const { register, handleSubmit, watch } = useForm({
        defaultValues: useMemo(() => {
            return {
                sellPrice: saved?.originPrice || '',
                changablePrice: saved?.changablePrice || false
            };
        }, [saved])
    });

    const sellPrice = watch('sellPrice');
    // const seat = watch('seat');
    // const qrcode = watch('qrcode');
    const onPrev = () => {
        setStep(2);
    };
    const onNext = () => {
        setStep(4);
    };

    const onSubmit = (data) => {
        setLoading(true);
        axios
            .post(API_TICKET.saveTicketPrcie, {
                ticketId: saved?._id,
                originPrice: data.sellPrice,
                ...data
            })
            .then((res) => {
                dispatch(setSavedTicket(res.data.data));
                onNext();
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    };
    // const addNewData = (e) => {
    //     setValue('seat', [...seat, '']);
    //     setValue('qrcode', [...qrcode, '']);
    //     e.preventDefault();
    // };
    // const removeData = (e, index) => {
    //     seat.splice(index, 1);
    //     qrcode.splice(index, 1);
    //     setValue('seat', [...seat]);
    //     setValue('qrcode', [...qrcode]);
    //     e.preventDefault();
    // };
    return (
        <FormProvider>
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="w-full justify-center flex flex-col gap-8 items-center px-2">
                    <div className="flex flex-col gap-8 w-full">
                        <div className="flex flex-col gap-2">
                            <h3 className="text-4xl md:text-[64px]">{t('title.set_price')}</h3>
                            <div className="grid md:grid-cols-2 text-stone-400">
                                <p>
                                    {t('description.set_price', {
                                        price: fNumber(saved?.sellPrice)
                                    })}
                                </p>
                            </div>
                        </div>
                        {/* seat & qr info */}
                        {/* {seat?.map((_, index) => (
                            <React.Fragment key={index}>
                                <div className="flex gap-2 w-full justify-between -mb-6">
                                    <span className="text-2xl font-bold" />
                                    <div className="flex gap-1 items-center">
                                        <button className="btn btn-circle btn-secondary btn-sm" onClick={addNewData}>
                                            <Icon icon="ic:outline-plus" className="text-primary" />
                                        </button>
                                        <button
                                            className="btn btn-circle btn-secondary btn-sm"
                                            onClick={(e) => removeData(e, index)}
                                        >
                                            <Icon icon="tabler:trash" />
                                        </button>
                                    </div>
                                </div>
                                <div className="flex gap-2 flex-col md:flex-row w-full">
                                    <div className="flex-1">
                                        <GradientBorderWrapper>
                                            <div className="flex w-full justify-between px-2 items-center">
                                                <Icon icon="ic:twotone-airline-seat-recline-extra" />
                                                <Controller
                                                    name="seat"
                                                    control={control}
                                                    render={({ field }) => {
                                                        const onChanged = (value) => {
                                                            field.value[index] = value;
                                                            return field.value;
                                                        };
                                                        return (
                                                            <input
                                                                className="input flex-1"
                                                                value={field.value[index]}
                                                                onChange={(e) =>
                                                                    field.onChange(onChanged(e.target.value))
                                                                }
                                                            />
                                                        );
                                                    }}
                                                />
                                            </div>
                                        </GradientBorderWrapper>
                                    </div>

                                    <div className="flex-1">
                                        <GradientBorderWrapper>
                                            <div className="flex w-full justify-between px-2 items-center">
                                                <Icon icon="uiw:qrcode" />
                                                <Controller
                                                    name="qrcode"
                                                    control={control}
                                                    render={({ field }) => {
                                                        const onChanged = (value) => {
                                                            field.value[index] = value;

                                                            return field.value;
                                                        };

                                                        return (
                                                            <input
                                                                className="input flex-1"
                                                                value={field.value[index]}
                                                                onChange={(e) =>
                                                                    field.onChange(onChanged(e.target.value))
                                                                }
                                                            />
                                                        );
                                                    }}
                                                />
                                            </div>
                                        </GradientBorderWrapper>
                                    </div>
                                </div>
                            </React.Fragment>
                        ))} */}

                        {/* price input */}
                        <div className='flex flex-col gap-4' >
                            <div className="grid grid-cols-4 gap-4 md:flex-row w-full mt-2.5">
                                <GradientBorderWrapper className="flex items-center" >
                                    <div className="flex w-full justify-between px-2 items-center">
                                        <h6>R$</h6>
                                    </div>
                                </GradientBorderWrapper>
                                <GradientBorderWrapper className="col-span-3">
                                    <div className="flex w-full justify-between px-2 items-center">
                                        <input type="number" className="input w-full" {...register('sellPrice')} />
                                        <h6 className="text-stone-400">/ingresso</h6>
                                    </div>
                                </GradientBorderWrapper>
                            </div>
                            <div className="grid grid-cols-2 gap-4 md:flex-row w-full">
                                <button className="btn btn-primary px-8 flex flex-col gap-1 h-16 text-[19px] capitalize">
                                    <b>{`R${fCurrency(sellPrice)}`}</b>
                                    Original
                                </button>
                                <button className="btn btn-secondary px-8 flex flex-col gap-1 h-16 text-[19px] capitalize">
                                    <b>{`R${fCurrency(sellPrice * 1.08)}`}</b>
                                    {t('action.maximum')}
                                </button>
                            </div>
                        </div>
                        <div className="flex gap-2 mb-4">
                            <label htmlFor="changablePrice">{t('title.changable_price')}</label>
                            <input
                                type="checkbox"
                                id="changablePrice"
                                className="toggle toggle-primary"
                                {...register('changablePrice')}
                            />
                        </div>
                        <div className="flex gap-2 flex-col md:flex-row w-full justify-between">
                            <div className="flex flex-col gap-2">
                                <h5 className="text-[28px] font-medium">{t('title.you_receive')}</h5>
                                <p className="text-stone-400 text-sm">{t('description.include_commission')}</p>
                            </div>
                            <div className="flex gap-1 items-center">
                                <h5 className="text-[28px] font-medium">R$ {fNumber(sellPrice * 1.05)}</h5>
                                <small className="text-stone-400 text-sm">/ingresso</small>
                            </div>
                        </div>
                        <div className="divider m-0" />
                        <div className="flex gap-2 flex-col md:flex-row w-full justify-between">
                            <div className="flex flex-col gap-2">
                                <h5 className="text-[28px] font-medium">{t('title.buyer_pay')}</h5>
                                <p className="text-stone-400 text-sm">{t('description.include_transaction')}</p>
                            </div>
                            <div className="flex gap-1  items-center">
                                <h5 className="text-[28px] font-medium">R$ {fNumber(sellPrice * 1.08)}</h5>
                                <small className="text-stone-400 text-sm">/ingresso</small>
                            </div>
                        </div>
                    </div>
                    {/* action buttons */}

                    <div className="w-full mb-8">
                        <div className="h-6" />

                        <div className="w-full flex justify-between">
                            <button className="btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto btn-outline" onClick={onPrev}>
                                {t('action.back')}
                            </button>
                            <button
                                type="submit"
                                disabled={!sellPrice}
                                className={`btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto ${loading && 'loading'}`}
                            >
                                {t('action.continue')}
                            </button>
                        </div>
                    </div>
                </div>
            </form>
        </FormProvider>
    );
}
